.App{
  display: flex;
  align-items: center;
  justify-items: center;
  align-content: center;
  background: #140F2D;
  justify-content: center;
  height: 100vh;
}
.item {
  flex: 1 0 18%;
  margin: 5px;
  height: 70px;
  color: #140F2D;
  display: flex;
  width: 70px;
  align-items: center;
  justify-content: center;
  background-color: #3F88C5;
  text-align: center;
  font-size: 13px;
  word-break: break-word;
}
.bingoWrapper{
  max-width: 700px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.row{
  display: flex;
  flex-direction: row;
}
.selected{
  background-color: #F49D37;
}